<template>
  <div class="product-container">
    <div class="product-item col-md-4" v-for='item in productList'>
    	<img class="product-show" :src="item.url">
    	<h1>{{item.id}}</h1>
    	<h1>售價: {{item.price}} Ntd</h1>
    	<h1>{{item.itemAccount}}</h1>
    	<button class="add-BTN" @click="add(item)">加入購物車</button>
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex'

export default {
  name: 'product',

  data () {
    return {
      
    }
  },

  computed:{
  	productList () {
  		return this.$store.state.product
  	}
  },

  methods: {
  	...mapActions([
  		'increment',
  		'addToCart',
  		'countTatal'
  	]),
  	add (item) {
  		this.increment();
  		this.addToCart(item);
  		this.countTatal();
  	}
  }
}
</script>

<style lang="stylus">
.product-container
  width 100%
  height auto
.product-show
	width 300px
	height 250px
.product-item
	text-align center
	border 1px solid lightgray
	padding 3%
.add-BTN
	padding 13px
	border-radius 5px
	box-shadow 1px 1px 1px
	&:focus
		outline-style none
</style>



