import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'
import normalize from 'normalize.css'
import $ from "jquery";
import boostrap from 'bootstrap'
import Products from '../components/product.vue'
import Main from '../components/main.vue'
import About from '../components/about.vue'
import AboutNZ from '../components/about-nz.vue'
import '../css/main.styl'
import store from './store.js'

Vue.use(VueRouter)

// const Products = System.import('../components/product.vue')
// const Main = System.import('../components/main.vue')
// const About = System.import('../components/about.vue')
// const AboutNZ = System.import('../components/about-nz.vue')

const routes = [
  { path: '/', component: Main },
  { path: '/products', component: Products },
  { path: '/about', component: About },
  { path: '/about-nz', component: AboutNZ }
]

const router = new VueRouter({
  routes 
})

const app = new Vue({
	el:'#app',
	store,
  router,
  render: h => h(App)
})



