import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
	count: 0, //物品數量
	total:0, //總價
	product:[
		{
			id: 'Trilogy玫瑰果油',
			price: 800,
			url: '../src/assets/trilogy.gif',
			itemCount: 1
		},
		{
			id: 'Royal Nectar面膜',
			price: 1000,
			url: '../src/assets/product1.gif',
			itemCount: 1
		},
		{
			id: 'NZ巧克力',
			price: 500,
			url: '../src/assets/product2.png',
			itemCount: 1
		},
	],
	product_in_cart_list: []
}

const mutations = {

	//顯示右上角購物車裡的物品數量
	increment (state) {
		state.count++
	},

	//如果沒有重複物件就直接加進去, 如果有重複該物件數量+1
	addToCart (state, item) {
		let productExsit;
		let cartList = state.product_in_cart_list
		if(cartList.length) {
			let itemExsit = cartList.filter((cartItem) => {
				return item.id === cartItem.id
			})
			itemExsit.length ? item.itemCount++ : cartList.push(item)
		} else {
			cartList.push(item)
		}
		
	},

	//count all the products price
	countTatal (state) {
		state.total = 0;
		state.product_in_cart_list.forEach((item) => {
			state.total += (item.price * item.itemCount)
		})
	},

	//remove item from shopping cart
	remove (state, product) {
		state.product_in_cart_list = state.product_in_cart_list.filter((item) => {
			return item.id !== product.id
		})
		state.count-=product.itemCount 
		state.total -= (product.price * product.itemCount)
		product.itemCount = 1
	}
}

const actions = {
	increment ({commit}) {
		commit('increment')
	},
	addToCart ({commit}, item) {
		commit('addToCart', item)
	},
	countTatal ({commit}) {
		commit('countTatal')
	},
	remove ({commit}, product) {
		commit('remove', product)
	}
}

const getter = {

}

export default new Vuex.Store({
	state,
	getter,
	actions,
	mutations
})



