<template>
  <div class="shopping-cart">
  	<div @click="toggle" class="shop-icon">
  		<span class="total-item">{{totalItemCount}}</span>
  		<img src="../src/assets/shopping-cart.png">
  	</div>
  	<div v-if="showOrHide" class="shop-detail">
  		<div class="product-in-cart">
  			<div class="individual-product" v-for="product in productInCart">
  				<img :src="product.url">
  				<span class="product-name">{{product.id}}</span>
  				<div class="quantity-wrapper">
  					<div class="quantity increase"></div>
  					<span class="">{{product.itemCount}}</span>
  					<div class="quantity decrease"></div>
  				</div>
  				<span class="product-price">{{product.price}} NTD</span>
  				<span class="remove" @click="remove(product)">X</span>
  			</div>
  		</div>
  		<h3 class="total">總價: {{totalAmount}} NTD</h3>
  	</div>
  </div>
</template>

<script>
import { mapActions } from 'vuex'

export default {
  name: 'header',
  data () {
    return {
      showOrHide: false
    }
  },

  computed:{
  	totalItemCount() {
  		return this.$store.state.count;
  	},
  	totalAmount () {
  		return this.$store.state.total;
  	},
  	productInCart () {
  		return this.$store.state.product_in_cart_list
  	}
  },

  methods:{
  	...mapActions([
  		'remove'
  	]),
  	toggle () {
  		this.showOrHide = !this.showOrHide
  	}
  }
}
</script>

<style lang="stylus">
.quantity-wrapper
	display inline-block
	background lightgray
	width 90px
	height 30px
	span
		background white
		width 30px
		height 28px
		margin 1px 0
		line-height 30px
		display inline-block
		text-align center
.quantity 
	display inline-block
	cursor pointer
	height 10px
	transition all 0.1s ease-in
	margin 10px
	background-repeat no-repeat
	width 10px
.product-name
	width 68%
	display inline-block
	text-align center
	font-size 21px
	font-family serif
.product-price
	font-family monospace
	font-size 17px
	width 68%
	text-align center
	display inline-block
.decrease
	background-image url(../src/assets/minus.png)
	background-position center center
	background-size 100%
	float right
.increase
	background-image url(../src/assets/plus.png)
	background-position center center
	background-size 100%
	float left
.shopping-cart
	position fixed
	z-index 1000
	top 0
	right 0
	background #f0f0f0
	cursor pointer
	padding 7px
.individual-product
	position relative
	margin 20px
	background white
	padding 2%
.remove
	position absolute
	top 5px
	right 8px
	font-size 19px
	font-weight 700
.shop-icon
	float right
	width 70px
	padding 10px
	img 
		width 33px
.shop-detail
	width 350px
	min-height 100px
.product-in-cart
	margin-top 60px
	min-height 50px
	border-bottom 2px solid black
	img
		width 30%
.total-item
	font-size 20px
.total
	float right
</style>



